import {Alert, Button, Col, Container, Row} from "react-bootstrap";
import MyTable from "./components/table";
import List from "./components/list";
import MyCard from "./components/card";

import {Data} from "./data";
import {TableData} from "./data/tableData";
import { useState} from "react";
import {Eraser, Printer, Save} from "react-bootstrap-icons";
import Footer from "./components/footer";
import MyNavbar from "./components/navbar";

function App() {

    const [tableData , setTableData] = useState(TableData);
    const [error , setError] = useState("")

    const handleCardClick = (data) => {
        setTableData(prev => prev.map((d) =>
            (d[0] === data.date[0][0] ? {...d,[data.time[0]]: '' } :
                d[0] === data.date[1][0] ? {...d,[data.time[1]]: '' } : {...d})
        ))
    }

    const checkTable = (data) => {
        let check = true
        tableData.forEach(d => {
            if (d[0] === data.date[0][0] && d[data.time[0]].length !== 0 ){
                setError(` ${data[' ']} (${data['']})  ${data.date[0][0]}  ${data.time[0]}  .`)
                check =  false;
            } else if (d[0] === data.date[1][0] && d[data.time[1]].length !== 0) {
                setError(` ${data[' ']} (${data['']})  ${data.date[1][0]}  ${data.time[1]}  .`)
                check =  false;
            }
        })

        return check;
    }

    const handleListClick = (data) => {

        if (checkTable(data)) {
            setTableData(prev => prev.map((d) =>
                (d[0] === data.date[0][0] ? {...d,[data.time[0]]: <MyCard data={data} handleCardClick={handleCardClick} state={data.date[0][1]} /> } :
                    d[0] === data.date[1][0] ? {...d,[data.time[1]]: <MyCard data={data} handleCardClick={handleCardClick} state={data.date[1][1]} /> } : {...d})
            ))
            setError('')
        }


    }



  return (
      <div>

      <MyNavbar setTableData={setTableData} />

      <Container className={'vh-100'}>
          <Row dir={'rtl'} className={'mt-3 align-items-center'}>
              <Col className={'col-9'}>
                  {error &&  <Alert variant={"danger"} >{error}</Alert> }

                  <MyTable  data={tableData} />

              </Col>
              <Col className={' col-3'}>
                  <List data={Data} onClick={handleListClick} />
              </Col>
          </Row>
          <Row>
              <Footer />
          </Row>
      </Container>
      </div>
  );
}

export default App;
