import {Button, Container, Nav, Navbar} from "react-bootstrap";
import {Eraser, Printer, Save} from "react-bootstrap-icons";
import {TableData} from "../../data/tableData";

import './style.css'

export default  function MyNavbar ({setTableData}) {
    return (
        <Navbar variant="light">
            <Container>
                <Navbar.Brand id={'brand'} href="#home">Fum Schedule - ComputerScience </Navbar.Brand>
                <div className={'text-end'}>
                    <Nav className="me-auto text-end mt-3">

                        <Button variant={'outline-success'} className={'mb-3'} onClick={() => {setTableData(TableData)}} >   <Eraser /></Button>
                        <Button variant={'outline-success'} className={'mb-3 mx-3'} onClick={''} > <Save /> </Button>
                        <Button variant={'outline-success'} className={'mb-3'} onClick={''} > <Printer /></Button>

                    </Nav>
                </div>

            </Container>
        </Navbar>
    )
}